<template lang="html">
            <div class="money-group1">
                <p class="money-tit">
                                       收款信息
                </p>
                <div class="xx qing">
                  <ul>
                  	<li><div>收款人姓名</div><div>张世豪</div></li>
                  	<li><div>联系手机</div><div>56789123413</div></li>
                  </ul>
                  <ul>
                  	<li><div>收款银行</div><div>中国农业</div></li>
                  	<li><div>开户行省份</div><div>天津</div></li>
                  </ul>
                  <ul>
                  	<li><div>银行卡号</div><div>62292863982</div></li>
                  	<li><div>开户行城市</div><div>天津</div></li>
                  </ul>
                </div>
            </div>
            </div>
</template>
<script>
   export default {
     data() {
    return {
      ruleForm: {
        name: '',
        bank:'',
        banknum:'',
        khbank:'',
        tel:'',
        sheng:'',
        city:''
      },
      rules: {
        name: [
          { required: true, message: '请输入收款人姓名', trigger: 'blur' },
          { type:'string', message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        bank: [
          { required: true, message: '请选择收款银行', trigger: 'change' }
        ],
        banknum:[
          { required: true, message: '请输入银行卡号', trigger: 'blur' }
        ],
        khbank: [
          { required: true, message: '请选择你开户的银行', trigger: 'change' }

        ],
        tel: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { min: 11, max: 11, message: '请输入正确的手机号', trigger: 'blur' }
        ],
        sheng: [
          { required: true, message: '请选择省份', trigger: 'change' }
        ],
        city: [
          { required: true, message: '请选择城市', trigger: 'change' }
        ],
      }
    };
  },
   }
</script>
<style lang="less">
  *{
    margin:0;
    padding:0;
  }
  .money-group1{
        border-bottom: 1px solid #E8E8F2;
        .money-tit{
          font-size: 22px;
          color: #2f55a6;
          font-weight: 900;
          margin: 32px 0;
        }
        .el-form-item{
           margin-bottom: 20px;
           .el-input{
             width:230px;
           }
        }
        .el-form--inline .el-form-item__label{
          font-weight: bold;
          color: #9DA2C8;
        }
      }
</style>



